@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  -webkit-user-drag: none;
  list-style: none;
  text-decoration: none;
  font-family: "Noto Sans KR", sans-serif;
  color: white;
}
html::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
html {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

body {
  background: #222;
}
.displayNone {
  display: none;
}
.displayFlex {
  display: flex;
  flex-wrap: wrap;
}
.flexCol {
  display: flex;
  flex-direction: column;
}
.overflowDot {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.textCenter {
  text-align: center;
}
.imgWidth {
  display: block;
  width: 100%;
}

.rem08 {
  font-size: 0.8rem;
}
.rem09 {
  font-size: 0.9rem;
}
.rem1 {
  font-size: 1rem;
}
.rem11 {
  font-size: 1.1rem;
}

@media screen and (max-width: 1024px) {
  .backgroundMyCoding {
    width: 1800px !important;
  }
}
@media screen and (min-width: 900px) {
  .dialogContent {
    width: 670px !important;
  }
}
@media screen and (max-width: 600px) {
  html {
    font-size: 14px;
  }
  .stackDivideWrap {
    width: 100% !important;
  }
  .pcProject {
    width: 100% !important;
    margin-top: 3% !important;
  }
  .mobileProject {
    width: 49% !important;
  }
  .backgroundMyCoding {
    width: 1500px !important;
  }
}

.intro {
  position: relative;
}
.myImg {
  width: 150px;
  height: 150px;
  background: url("/portfolio/img/sangmin.jpg");
  border-radius: 50%;
  background-position: 50% 10%;
  background-size: cover;
  overflow: hidden;
  margin: 0 auto;
}
.introCont {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  position: absolute;
  left: 50%;
  bottom: -15%;
  transform: translate(-50%);
  background: rgba(0, 0, 0, 0.4);
  border-radius: 3px;
  padding: 0.2rem 0.4rem;
}

.section:nth-of-type(1) {
  padding-top: 20px;
}
.section:nth-of-type(1) .sectionCont {
  align-items: center;
}
.emailTitle {
  background: #222;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 0.2rem 0.4rem;
  border-radius: 3px;
  margin-right: 1rem;
}

.section:nth-of-type(2) .sectionCont {
  justify-content: space-between;
  flex-wrap: wrap;
}
.stackDivideWrap {
  width: 49%;
  border-radius: 0 0 3px 3px;
  margin-bottom: 2%;
  border: 1px solid white;
}
.stackDivide {
  background: #222;
  border-radius: 3px 3px 0 0;
  text-align: center;
}
.stackWrap {
  padding: 0.2rem;
}
.stack {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
  align-items: center;
}
.stackImg {
  width: 20px;
  height: 20px;
  background-size: 100% !important;
  margin-right: 0.5rem;
}

.linkBtn {
  margin-right: 0.8rem;
}
.linkBtn:last-child {
  margin-right: 0;
}

.section:nth-of-type(3) .sectionCont {
  display: flex;
  justify-content: center;
}
.linkBtn {
  position: relative;
  cursor: pointer;
}
.section:nth-of-type(3) .linkBtn::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  right: 100%;
  top: 4px;
  background-size: cover;
}
.section:nth-of-type(3) .linkBtn:first-child {
  margin-right: 3.5rem;
}
.section:nth-of-type(3) .linkBtn:first-child::before {
  background-image: url("/portfolio/img/git.png");
}
.section:nth-of-type(3) .linkBtn:last-child::before {
  background-image: url("/portfolio/img/blog.png");
}

.projectPop {
  padding: 0.5rem;
}
/* 프로젝트 링크 */
.projectLinkWrap {
  display: flex;
  justify-content: center;
  margin-bottom: 0.5rem;
}
.projectLink {
  margin-right: 1rem;
  cursor: pointer;
}
/* 프로젝트 
슬라이드 이미지 */
.projectImgpc {
  width: 80%;
  margin: 0 auto;
}
.projectImgmobile {
  width: 40%;
  margin: 0 auto;
}
.imgWrap {
  position: relative;
  width: 100%;
  overflow: hidden;
  cursor: pointer;
}
.pcImgWrap {
  padding-bottom: 51%;
}
.mobileImgWrap {
  padding-bottom: 178%;
}
.slideBtn {
  width: 5%;
  box-sizing: content-box;
  position: absolute;
  padding: 0.2rem 0.3rem;
  border-radius: 3px;
  bottom: 6%;
  z-index: 1;
  cursor: pointer;
  background: rgba(0, 0, 0, 0.5);
}
.prev {
  left: 3%;
}
.next {
  right: 3%;
}

.imgWrap .slideImg {
  display: block;
  position: absolute;
}

/* projectSection */
.projectSection,
.projectSection2 {
  margin-top: 0.5rem;
}
.projectPopTitle {
  background: #222;
  padding: 0.1rem 0.3rem;
  border-radius: 1px;
}
.projectSection .projectPopTitle {
  margin-right: 0.3rem;
}
/* 사용스택 */
.used {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 0.2rem 0.4rem;
}
.projectSection2 .flex {
  flex-wrap: wrap;
}
/* 상세설명 */
.projectPopCont .dsec {
  padding: 0.2rem 0.4rem;
}

.section:nth-of-type(4) .sectionCont {
  flex-wrap: wrap;
  justify-content: space-between;
}
.projectTabWrap {
  display: flex;
  width: 100%;
  justify-content: center;
}
.projectTabWrap > div {
  margin-right: 3.5rem;
  cursor: pointer;
}
.projectTabWrap > div:last-child {
  margin-right: 0;
}
/*  */
.projectsWrap {
  justify-content: space-between;
}
.pcProject {
  width: 49%;
  background: #222;
  overflow: hidden;
  position: relative;
  margin-top: 2%;
  border-radius: 3px;
}
.mobileProject {
  width: 24%;
  max-height: 280px;
  background: #222;
  overflow: hidden;
  position: relative;
  margin-top: 2%;
  border-radius: 3px;
}
.projectBg {
  position: absolute;
  cursor: pointer;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  padding: 0.3rem;
  background: rgba(0, 0, 0, 0.6);
  transition: 0.3s;
}
.projectBg:hover {
  background: rgba(0, 0, 0, 0.8);
}
.pointer {
  width: 10%;
}
.pointermobile {
  width: 20%;
}
.refactoring {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
  border: 1px solid #eaeaea;
  border-radius: 3px;
  padding: 0.2rem 0.3rem;
  color: #eaeaea;
}

.dialogBg {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  cursor: pointer;
}
.dialogContent {
  position: fixed;
  z-index: 3;
  background: #444;
  width: 70%;
  min-width: 250px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  max-height: 80%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -58%);
  overflow-y: scroll;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
  opacity: 0;

  -webkit-animation: dialogAni;

          animation: dialogAni;
  -webkit-animation-duration: 0.8s;
          animation-duration: 0.8s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.dialogContent::-webkit-scrollbar {
  display: none;
}
@-webkit-keyframes dialogAni {
  0% {
    opacity: 0;
    transform: translate(-50%, -58%);
  }
  100% {
    opacity: 1;
    transform: translate(-50%, -50%);
  }
}
@keyframes dialogAni {
  0% {
    opacity: 0;
    transform: translate(-50%, -58%);
  }
  100% {
    opacity: 1;
    transform: translate(-50%, -50%);
  }
}

.App {
  position: relative;
  min-width: 310px;
}
.backgroundMyCoding {
  width: 3000px;
  height: 100vh;
  position: fixed;
  z-index: -1;
}
.backgroundMyCoding img {
  width: 100%;
  position: absolute;
  transition: 4s;
}
.innerApp {
  position: relative;
  max-width: 900px;
  min-width: 310px;
  margin: 0 auto;
  padding: 5.5rem 0.5rem 3rem 0.5rem;
}
/* layout */
/* section */
.section {
  margin-top: 25px;
}
.sectionWrap {
  width: 100%;
  border-radius: 3px;
  padding: 0.7rem;
  background: #444;
}
.sectionTitle {
  text-align: center;
  margin-bottom: 10px;
  background: #222;
  border-radius: 3px 3px 0 0;
}
.sectionCont {
  display: flex;
}
/* sectionMoveTabWrap */
.sectionMoveTabWrap {
  width: 100%;
  position: fixed;
  bottom: 0;
  z-index: 1;
  background: rgba(0, 0, 0, 0.6);
}
.sectionMoveTabInnerWrap {
  max-width: 900px;
  min-width: 310px;
  margin: 0 auto;
  padding: 0.5rem;
  display: flex;
}
.sectionMoveTab {
  flex-grow: 1;
  cursor: pointer;
}

